import React from "react";
import {useSelector} from "react-redux";
import {RootState} from "app/reducers";
import CircularProgress from '@material-ui/core/CircularProgress';
import Typography from '@material-ui/core/Typography';
import StarIcon from '@material-ui/icons/Star';
import style from "app/components/AnalyzeProgress/style.scss";

export const AnalyzeProgress = (): JSX.Element => {
    const isLoading = useSelector((state: RootState) => state.analyzedData.isLoading);
    const isEmpty = useSelector((state: RootState) => state.analyzedData.isEmpty);
    const isFinished = useSelector((state: RootState) => state.analyzedData.isFinished);
    const isNotFinished = useSelector((state: RootState) => state.analyzedData.isNotFinished);

    const loader = <CircularProgress className={style.iProgress}/>;

    return (
        <div className={style.progressRoot}>
            {isLoading && loader}
            {isEmpty && !isLoading && <Typography  className={style.stepText}>Nothing to check yet</Typography>}
            {isNotFinished && !isLoading && <Typography  className={style.stepText}>You are almost there!</Typography>}
            {isFinished && !isLoading && <Typography  className={style.stepText}><StarIcon className={style.iStar}/> Ready to go!</Typography>}
        </div>
    );
};