import {ErrorItem} from "app/models/ErrorItem";

export const selectAllText = (el: any) => {
    el.focus();
    document.execCommand('selectAll', false, undefined);
}

export function copyToClipboard(text: string): void {
    navigator.clipboard.writeText(text);
}

export const getErrorsList = (data: []) => {
    let errorsList = data.reduce((acc: any, curr: ErrorItem) => {
        const {existingTokens, errorType, errorId} = curr;
        existingTokens.map((item:any) => {
            item.id = errorId;
            item.type = errorType;
            acc.push(item)
        });
        return acc;
    }, [])
    errorsList.sort((a: any, b: any) =>  b.beginOffset - a.beginOffset)

    return errorsList;
}

export const setCurrentCursorPosition = (el: any, chars: number) => {
    if (chars >= 0) {
        var selection = window.getSelection();

        let range = createRange(el, { count: chars } );

        if (range && selection !== null) {
            range.collapse(false);
            selection.removeAllRanges();
            selection.addRange(range);
        }
    }
};

export const createRange = (node: any, chars: any, range?: any) => {
    if (!range) {
        range = document.createRange()
        range.selectNode(node);
        range.setStart(node, 0);
    }

    if (chars.count === 0) {
        range.setEnd(node, chars.count);
    } else if (node && chars.count >0) {
        if (node.nodeType === Node.TEXT_NODE) {
            if (node.textContent.length < chars.count) {
                chars.count -= node.textContent.length;
            } else {
                range.setEnd(node, chars.count);
                chars.count = 0;
            }
        } else {
            for (var lp = 0; lp < node.childNodes.length; lp++) {
                range = createRange(node.childNodes[lp], chars, range);

                if (chars.count === 0) {
                    break;
                }
            }
        }
    }

    return range;
}

export const getCaretCharacterOffsetWithin = (element: any) => {
    var caretOffset = 0;
    var doc = element.ownerDocument || element.document;
    var win = doc.defaultView || doc.parentWindow;
    var sel;
    if (typeof win.getSelection != "undefined") {
        sel = win.getSelection();
        if (sel.rangeCount > 0) {
            var range = win.getSelection().getRangeAt(0);
            var preCaretRange = range.cloneRange();
            preCaretRange.selectNodeContents(element);
            preCaretRange.setEnd(range.endContainer, range.endOffset);
            caretOffset = preCaretRange.toString().length;
        }
    } else if ( (sel = doc.selection) && sel.type != "Control") {
        var textRange = sel.createRange();
        var preCaretTextRange = doc.body.createTextRange();
        preCaretTextRange.moveToElementText(element);
        preCaretTextRange.setEndPoint("EndToEnd", textRange);
        caretOffset = preCaretTextRange.text.length;
    }
    return caretOffset;
}