import React from "react";
import Tooltip from "@material-ui/core/Tooltip";
import { makeStyles } from '@material-ui/core/styles';
import style from './style.scss';
import { CategoryItem } from "app/types";

const useStyles = makeStyles(() => ({
    arrow: {
        left: '30px !important',
        color: '#616368',
    },
    tooltip: {
        padding: '16px',
        fontWeight: 400,
        fontSize: '14px',
        fontStyle: 'italic',
        lineHeight: '21px',
        background: '#616368',
        margin: '10px 0'
    },
}));


export const Category = (props: {category: CategoryItem, tooltipText: string}): JSX.Element => {
    const {category, tooltipText} = props;
    const {tooltip, arrow} = useStyles();

    return (
        <div className={style.category}>
            <Tooltip
                title={tooltipText}
                arrow
                classes={{tooltip: tooltip, arrow: arrow}}
                placement='top-start'
            >
                <span>
                    {category.name}
                    <span className={style.count}>{category.score}</span>
                </span>
            </Tooltip>
        </div>
    );
};