import style from "./style.scss";
import React from "react";

interface LinkProps {
    text: string;
    url?: string;
    onClick?: () => void;
}

export const Link = (props: LinkProps): JSX.Element => (
    <a className={style.link}
       href={props.url}
       onClick={props.onClick}
    >
        { props.text }
    </a>
);