import {baseUrl} from "app/constants";
import {Token} from "app/models/ErrorItem";

export const ANALYZE_REQUEST_START = 'ANALYZE_REQUEST_START';
export const ANALYZE_SUCCESS = 'ANALYZE_SUCCESS';
export const ANALYZE_ERROR = 'ANALYZE_ERROR';
export const SELECT_ERROR = 'SELECT_ERROR';
export const DISMISS_ERROR = 'DISMISS_ERROR';
export const USE_SUGGESTION = 'USE_SUGGESTION';
export const DISLIKE_ERROR_START = 'DISLIKE_ERROR_START';
export const DISLIKE_ERROR_SUCCESS = 'DISLIKE_ERROR_SUCCESS';
export const DISLIKE_ERROR_FAIL = 'DISLIKE_ERROR_FAIL';

export const analyzeRequest  = () => {
    return {
        type: ANALYZE_REQUEST_START
    }
}
export const analyzeSuccess  = (result: any) => {
    return {
        type: ANALYZE_SUCCESS,
        payload: {items: result}
    }
}
export const analyzeFail = (error: string) => {
    return {
        type: ANALYZE_ERROR,
        error
    }
}

export const selectError = (errorId: string) => {
    return {
        type: SELECT_ERROR,
        payload: { errorId }
    };
}

export const dismissError = (errorId: string) => {
    return {
        type: DISMISS_ERROR,
        payload: { errorId }
    };
}

export const useSuggestion = (errorId: string, suggestion: Token) => {
    return {
        type: USE_SUGGESTION,
        payload: { errorId, suggestion }
    };
}

export const dislikeError = (errorId: string) => {
    return {
        type: DISLIKE_ERROR_START,
        payload: { errorId }
    };
}
export const dislikeErrorSuccess  = (errorId: string) => {
    return {
        type: DISLIKE_ERROR_SUCCESS,
        payload: { errorId }
    }
}
export const dislikeErrorFail = (error: string) => {
    return {
        type: DISLIKE_ERROR_FAIL,
        error
    }
}

export const fetchAnalyzedData = (inputVal: string) => (dispatch: any) => {
    dispatch(analyzeRequest());
    return fetch(`${baseUrl}/analyze`, {
        method: 'POST',
        body: JSON.stringify({
            textData: inputVal
        }),
    }).then(response => {
        if (!response.ok) {
            dispatch(analyzeFail('fetch failed'))
            throw new Error('Something went wrong');
        }
        response.json().then(data => {
            console.log('data', data)
            dispatch(analyzeSuccess(data));
        });
    }).catch((error) => {
        console.log('error', error);
    });
}

export const postDislikeError = (errorId: string) => (dispatch: any) => {
    dispatch(dislikeError(errorId));
    dispatch(dislikeErrorSuccess(errorId));
}