import React from "react";

export const TextWrapper = (props: {selected: boolean, id: string, onClick: any, text: string, type: string}) => {
    const {selected, id, onClick, type, text} = props;
    let classes = type;
    if(selected){
        classes = classes.concat(' ', 'selected')
    }
    return (
        <span onClick={onClick}  data-error-id={id} className={classes}>{text}</span>
    )
}