import React from "react";
import FileCopyOutlinedIcon from "@material-ui/icons/FileCopyOutlined";
import IconButton from "@material-ui/core/IconButton";
import {copyToClipboard} from "app/utils/helpers";

import style from './style.scss';

export const AcceptanceCriteriaCard = (props: any): JSX.Element => {
    const {title} = props;
    const criterias = [
        'Coming soon...'
        // '1. The system shall display button "Economy of data with calls".',
        // '1.2. When user clicks button "Economy of data with calls", the system shall display Economy of data with calls list.',
        // '2. The system shall display "Text answers" button.',
        // '2.1. When user clicks button ""Text answers"", the system shall display Text answers list. "'
    ];


    return (
        <div className={style.card}>
            <div className={style.title}>{title}</div>
            <div className={style.content}>
                { criterias.map((c, i) => <div key={i}>{ c }</div>) }
            </div>
            <div className={style.footer}>
                <IconButton onClick={() => copyToClipboard(criterias.join('\n'))}>
                    <FileCopyOutlinedIcon  className={style.copyIcon} />
                </IconButton>
            </div>
        </div>
    );
};