import React from "react";

import style from './style.scss';

interface PredictionBarProps {
    /** Integer number from 0 to 100. */
    value: number;
}

export const PredictionBar = (props: PredictionBarProps): JSX.Element => {
    const value = `${props.value}%`;
    return (
        <div className={style.predictionBar}>
            <div className={style.bar}>
                <div className={style.progress} style={{ width: value }} />
            </div>
            <div className={style.label}>{ value }</div>
        </div>
    );
};