import React, {useState} from "react";
import {ErrorsList} from "app/components/ErrorsList";
import {AcceptanceCriteriaList} from "app/components/AcceptanceCriteriaList";

import style from "./style.scss";

export const TabBar = (): JSX.Element => {
    const [activeTab, setActiveTab] = useState(0);

    return (
        <React.Fragment>
            <div className={style.tabBar}>
                <div
                    className={`${style.tab} ${activeTab === 0 ? style.active : ''}`}
                    onClick={() => setActiveTab(0)}
                >
                    Correctness
                </div>
                <div
                    className={`${style.tab} ${activeTab === 1 ? style.active : ''}`}
                    onClick={() => setActiveTab(1)}
                >
                    Acceptance criteria
                </div>
            </div>
            <div className={style.tabContent}>
                { activeTab === 0 && <ErrorsList /> }
                { activeTab === 1 && <AcceptanceCriteriaList /> }
            </div>
        </React.Fragment>
    );
}