import React, {useEffect, useRef, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import style from './style.scss';
import FileCopyOutlinedIcon from '@material-ui/icons/FileCopyOutlined';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import {fetchAnalyzedData} from 'app/actions';
import {copyToClipboard, getErrorsList, selectAllText , getCaretCharacterOffsetWithin, setCurrentCursorPosition} from 'app/utils/helpers';
import {FormInnerValue} from './components/FormInnerValue';

export const AnalyzeForm = (): JSX.Element => {
    const dispatch = useDispatch();
    const data = useSelector((state: any) => state.analyzedData.items);
    const [noValueInput, setNoValueInput] = useState(true);
    const [inputRawValue, setInputRawValue] = useState('');
    const [errors, setErrors] = useState([])
    const inputRef = useRef<HTMLDivElement>(null);
    const originalVal = useRef('');
    const getInputValue = () => {
        return (inputRef && inputRef.current && inputRef?.current?.innerHTML !== '<br>') ? inputRef.current.innerText : '';
    }

    const analyzeReqs = () => {
        if(getInputValue()){
            originalVal.current = getInputValue();
            setInputRawValue(getInputValue());
            dispatch(fetchAnalyzedData(getInputValue()))
        }
    }

    const handleCopyToClipboard = () => {
        if(inputRef && inputRef.current) {
            copyToClipboard(inputRef.current.innerText)
            selectAllText(inputRef.current)
        }
    }

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const el = inputRef.current;
        const sel = window.getSelection();

        if(el !== null && sel !== null){
            if(sel.rangeCount){
                let caretPos = getCaretCharacterOffsetWithin(el);
                setInputRawValue(getInputValue());
                setErrors([])
                setTimeout(() => {
                    setCurrentCursorPosition(el, caretPos)
                }, 0)
            }
        }

        setNoValueInput(!e.target.innerText);
    }

    const handleKeyDown = (e: any) => {
        console.log('e', e)
    }

    useEffect(() => setErrors(getErrorsList(data)), [data])

    return (
        <div className={style.reqForm}>
            <div className={style.reqContainer}>
                {noValueInput && <div className={style.reqPlaceholder}>Type or paste your text here.</div>}

                <FormInnerValue
                    inputRef={inputRef}
                    className={style.reqInput}
                    onInput={handleInputChange}
                    onKeyDown={handleKeyDown}
                    errors={errors}
                    inputValue={inputRawValue}
                    originalVal={originalVal.current}
                />
            </div>
            <footer className={style.reqBlockFooter}>
                <div className={style.reqInfo}>Keep one requirement or user story at a time</div>
                <IconButton className={style.reqCopyBtn} onClick={handleCopyToClipboard}>
                    <FileCopyOutlinedIcon />
                </IconButton>

                <Button onClick={analyzeReqs} className={style.reqAnalyzeBtn} variant="outlined">Analyze</Button>
            </footer>
        </div>
    );
};