import React from 'react';
import {ErrorCardHeader} from "app/components/ErrorCard/ErrorCardHeader";
import {ErrorCardBody} from "app/components/ErrorCard/ErrorCardBody";
import {ErrorItem} from "app/models/ErrorItem";

import style from './style.scss';

interface ErrorCardProps {
    error: ErrorItem;
    expanded?: boolean;
    disliked?: boolean;
}

export const ErrorCard = (props: ErrorCardProps): JSX.Element => {
    return (
        <div className={style.errorCard}>
            <ErrorCardHeader error={props.error} expanded={props.expanded} disliked={props.disliked} />
            { props.expanded && <ErrorCardBody error={props.error} /> }
        </div>
    );
};









