import React from "react";
import {AcceptanceCriteriaCard} from "app/components/AcceptanceCriteriaCard";

import style from './style.scss';

export const AcceptanceCriteriaList = (): JSX.Element => {
    return (
        <div className={style.listWrapper}>
            <AcceptanceCriteriaCard title="Functional"/>
            <AcceptanceCriteriaCard title="Non-Functional"/>
        </div>
    );
};