import React from "react";
import {useDispatch} from "react-redux";
import ThumbDownAltIcon from "@material-ui/icons/ThumbDownAlt";
import UnfoldMoreIcon from "@material-ui/icons/UnfoldMore";
import {postDislikeError, selectError} from "app/actions";
import {CrossedText} from "app/components/ErrorCard/CrossedText";
import {PredictionBar} from "app/components/ErrorCard/PredictionBar";
import {ErrorItem} from "app/models/ErrorItem";

import style from "./style.scss";

interface ErrorCardHeaderProps {
    error: ErrorItem;
    expanded?: boolean;
    disliked?: boolean;
}

export const ErrorCardHeader = (props: ErrorCardHeaderProps): JSX.Element => {
    const dispatch = useDispatch();
    const errorItem = props.error;
    const prediction = Math.round(errorItem.prediction * 100);
    const errorText = errorItem.existingTokens[0].text;

    const statusClasses = `${style.status} ${style[errorItem.errorType]}`;
    const status = <div className={statusClasses} />;

    const headerMainPart = (
        <div className={style.mainPart}>
            {
                props.expanded ?
                    <div className={style.errorTitle}>{ errorItem.error }</div> :
                    <CrossedText text={errorText} />
            }
        </div>
    );

    const dislikeButtonClasses = `${style.dislikeButton} ${props.disliked ? style.active : ''}`;
    const dislikeButton = (
        <div className={dislikeButtonClasses} onClick={() => {
            if (props.disliked) {
                return;
            }
            console.log('clicked');
            dispatch(postDislikeError(errorItem.errorId));
        }}>
            <ThumbDownAltIcon className={style.dislikeIcon} />
            <div>Incorrect error</div>
        </div>
    );

    const expand = () => {
        if (props.expanded) {
            return;
        }
        dispatch(selectError(errorItem.errorId));
    };

    const headerStyles = `${style.header} ${!props.expanded ? style.collapsed : ''}`;

    return (
        <div className={headerStyles} onClick={expand}>
            { status }
            { headerMainPart }
            <div className={style.predictionBarWrapper}>
                <PredictionBar value={prediction} />
            </div>
            <UnfoldMoreIcon className={style.expandIcon}/>
            { props.expanded ? dislikeButton : null }
        </div>
    )
};