import React from "react";
import {useSelector} from "react-redux";
import {RootState} from "app/reducers";
import Typography from '@material-ui/core/Typography';
import style from './style.scss';
import {Category} from './components/Category';
import { CategoryItem } from "app/types";

export const Categories = (): JSX.Element => {
    const items = useSelector((state: RootState) => state.analyzedData.categories);
    const tooltipText = 'The requirement defines an essential capability, characteristic, constraint, or quality factor.';
    const categories = items.map((c:CategoryItem, i: number) => (
        <Category key={i} category={c} tooltipText={tooltipText} />
    ));

    return (
        <div className={style.categoriesRoot}>
            <Typography className={style.heading}>Categories</Typography>
            {(items && (items.length > 0)) && categories}
        </div>
    );
};