import React from "react";
import style from './style.scss';

interface CrossedTextProps {
    text: string;
}

export const CrossedText = (props: CrossedTextProps): JSX.Element | null => {
    if (!props.text) {
        return null;
    }
    return (
        <div className={style.crossedText}>
            &nbsp;&nbsp;{ props.text }&nbsp;&nbsp;
        </div>
    );
};