import React from 'react';
import {useSelector} from "react-redux";
import {RootState} from "app/reducers";
import {ErrorCard} from "app/components/ErrorCard";
import {ErrorItem} from "app/models/ErrorItem";

import style from './style.scss';

export const ErrorsList = (): JSX.Element => {
    const items = useSelector((state: RootState) => state.analyzedData.items);
    const isLoading = useSelector((state: RootState) => state.analyzedData.isLoading);
    const selectedErrorId = useSelector((state: RootState) => state.analyzedData.selectedErrorId);
    const dislikedErrorIds = useSelector((state: RootState) => state.analyzedData.dislikedErrorIds);

    const errors = items.map((e: ErrorItem) => (
        <ErrorCard
            key={e.errorId}
            error={e}
            expanded={e.errorId === selectedErrorId}
            disliked={dislikedErrorIds.includes(e.errorId)}
        />
    ));

    return (
        <div className={style.errorsList}>
            {!isLoading && errors }
        </div>
    );
};