import {
    ANALYZE_REQUEST_START,
    ANALYZE_SUCCESS,
    ANALYZE_ERROR,
    SELECT_ERROR,
    DISMISS_ERROR,
    DISLIKE_ERROR_SUCCESS,
    USE_SUGGESTION
} from "app/actions";
import {AnalyzedResult, CategoryItem} from '../types';
import { ErrorItem, Token } from "app/models/ErrorItem";

export interface AnalyzeState {
    items: ErrorItem[];
    categories: CategoryItem[];
    selectedErrorId: string;
    dislikedErrorIds: string[];
    suggestion: Token | null;
    isLoading: boolean;
    error: string | null;
    isEmpty: boolean;
    isFinished: boolean;
    isNotFinished: boolean;
}

const initialState: AnalyzeState = {
    items: [],
    categories: [],
    selectedErrorId: '',
    dislikedErrorIds: [],
    suggestion: null,
    isLoading: false,
    isEmpty: true,
    isFinished: false,
    isNotFinished: false,
    error: null
}

export const analyzeReducer = (state = initialState, action: AnalyzedResult) => {
    const { type, payload, error } = action;
    switch (type) {
        case ANALYZE_REQUEST_START:
            return {
                ...initialState,
                isLoading: true
            }
        case ANALYZE_SUCCESS:
            return {
                ...state,
                isLoading: false,
                items: payload.items.errors,
                categories: payload.items.scoring,
                error: null,
                isEmpty: false,
                isFinished: false,
                isNotFinished: true
            }
        case ANALYZE_ERROR:
            return {
                ...state,
                isLoading: false,
                error: error
            }
        case SELECT_ERROR:
            return {
                ...state,
                selectedErrorId: payload.errorId
            }
        case DISMISS_ERROR:
            return {
                ...state,
                items: state.items.filter((item: ErrorItem) => item.errorId !== payload.errorId)
            }
        case USE_SUGGESTION:
            return {
                ...state,
                suggestion: payload.suggestion
            }
        case DISLIKE_ERROR_SUCCESS:
            return {
                ...state,
                dislikedErrorIds: [...state.dislikedErrorIds, payload.errorId]
            }
        default:
            return state;
    }
}