import React from 'react';
import {TextWrapper} from "../TextWrapper";
import {useDispatch, useSelector} from "react-redux";
import {selectError} from "app/actions";
import {FormError} from "app/types";
import {RootState} from "app/reducers";

export const FormInnerValue = (props: any) => {
    const {errors, onInput, onKeyDown, className, inputRef, originalVal = '', inputValue} = props;
    const dispatch = useDispatch();
    const selectedErrorId = useSelector((state: RootState) => state.analyzedData.selectedErrorId);
// console.log('errors', errors)
    let tempStr = originalVal;
    const handleClick = (i: number, e: any) => {
        const errorId = e.target.getAttribute('data-error-id');
        dispatch(selectError(errorId));
    }
    const handleKeyDown = (e: any) => {
        onKeyDown(e)
    }
    let result = '';

    result = errors.reduce((acc: any, curr: FormError, i: number, array: []) => {
        const begin = curr.beginOffset;
        const end = begin + curr.text.length;
        const cleanText = tempStr.slice(end)
        const errorTextComponent = <TextWrapper
            key={curr.id}
            id={curr.id}
            selected={curr.id === selectedErrorId}
            onClick={(e:any) => handleClick(i, e)}
            text={tempStr.slice(begin, end)}
            type={curr.type}/>
        tempStr = tempStr.slice(0, begin)
        acc.unshift(errorTextComponent, cleanText)
        if (i === (array.length - 1)) {
            acc.unshift(tempStr)
        }
        return acc;
    }, [])

    let final = '';

    if (errors && errors.length) {
        final = result;
    } else {
        final = inputValue;
    }

    return (
        <div
            ref={inputRef}
            contentEditable={true}
            className={className}
            suppressContentEditableWarning={true}
            onInput={onInput}
            onKeyDown={(e:any) => handleKeyDown(e)}
            spellCheck={false}
        >
            {final}
        </div>
    )
};