import React, {useState} from "react";
import {useDispatch} from "react-redux";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import VisibilityOffIcon from "@material-ui/icons/VisibilityOff";
import {dismissError, useSuggestion} from "app/actions";
import {CrossedText} from "app/components/ErrorCard/CrossedText";
import {SuggestionItem} from "app/components/ErrorCard/SuggestionItem";
import {Link} from "app/components/ErrorCard/Link";
import {ErrorItem, Token} from "app/models/ErrorItem";

import style from './style.scss';

interface ErrorCardBodyProps {
    error: ErrorItem;
}

export const ErrorCardBody = (props: ErrorCardBodyProps): JSX.Element | null => {
    const dispatch = useDispatch();
    const [detailsVisible, setDetailsVisible] = useState(false);

    const errorItem = props.error;
    const errorText = errorItem.existingTokens[0].text;
    const suggestions = errorItem.newTokens !== undefined
        ? errorItem.newTokens
        : [];

    const onSuggestionClicked = (suggestion: Token) => {
        dispatch(useSuggestion(errorItem.errorId, suggestion));
    };
    const suggestionsBlock = (
        <div className={style.suggestionsBlock}>
            <CrossedText text={errorText} />
            {
                suggestions.length > 0
                    ? <React.Fragment>
                        <ArrowForwardIcon className={style.arrowIcon}/>
                        <div>
                            { suggestions.map((s, i) => {
                                return <SuggestionItem text={s.text} key={i} onClick={() => onSuggestionClicked(s)} />;
                            }) }
                        </div>
                    </React.Fragment>
                    : null
            }
        </div>
    );

    const showDetails = () => setDetailsVisible(true);
    const hideDetails = () => setDetailsVisible(false);

    return (
        <div className={style.body}>
            <div className={style.content}>
                { suggestionsBlock }
                <div className={style.description}>{ errorItem.details }</div>
                {
                    detailsVisible ?
                        <React.Fragment>
                            <Link text="Hide details" onClick={hideDetails} />
                            <div className={style.details}>{ errorItem.longDescription }</div>
                        </React.Fragment> :
                        <Link text="Show details" onClick={showDetails} />
                }
            </div>
            <div className={style.footer}>
                <div className={style.dismissButton} onClick={() => dispatch(dismissError(errorItem.errorId))}>
                    <VisibilityOffIcon className={style.dismissIcon}/>
                    Dismiss
                </div>
            </div>
        </div>
    )
};