import React from 'react';
import { RouteComponentProps } from 'react-router';
import Grid from '@material-ui/core/Grid';
import style from './style.scss';
import { AnalyzeForm, Categories, AnalyzeProgress } from 'app/components';
import { TabBar } from "app/components/TabBar";

export namespace App {
  export interface Props extends RouteComponentProps<void> {}
}

export const App = () => {
    return (
        <div className={style.appRoot}>
            <Grid className={style.appContainer} container spacing={3}>
                <Grid item xs={6}>
                    <AnalyzeForm />
                    <Grid className={style.categoriesContainer} container>
                        <Grid item xs={6}>
                            <Categories/>
                        </Grid>
                        <Grid item xs={6}>
                            <AnalyzeProgress />
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={6}>
                    <TabBar />
                </Grid>
            </Grid>
        </div>
    );
};
